import React, { useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import './Cart.css'
import {ReactComponent as DeleteIcon} from '../../images/delete.svg';
import {ReactComponent as BackIcon} from '../../images/leftIcon.svg';
import { ImageBehaviour, Images } from '../productsView/productsComponent/Images';
import useWindowSize from '../../hooks/UseWindowSize';
import { Helmet } from 'react-helmet';
import {ReactComponent as CartIcon} from '../../images/basket.svg';
import { getRoute } from '../../services/routes.service';
import { CartContext } from '../../context/cart-context';
import { Empty } from './Empty';
import { Ad } from './Ad';
import { Price } from '../productsView/productsComponent/Price';
import { formatPrice } from '../helpers/formatters';

export default function Basket() {
    const { removeItem, cart, handleChange, totalProductPrice, endedPrice, endedSale, count } = useContext(CartContext);

    let size = useWindowSize();
    const isMobile = size.width < 770
    const navigate = useNavigate();

    const onConfirm = () => {
        if (cart.data.length > 0)
        navigate(getRoute(`user_info`))
    };

    const handleRemove = (product: IProductCart) => {
        removeItem((cart: IProductCart[]) => cart.filter(item => !(item.id === product.id && item.color === product.color && item.size === product.size)));
    };

    const viewProducts = cart.data.map((item:IProductCart, id: number) => {
        return(
            <div className='p-3 bg-white' key={id} >
                <div className='flex items-center'> 

                    <div className='imgPosition relative' >
                        <div onClick={() => navigate(getRoute(`product/${item.id}`))}><Images images={item.images} behaviour={ImageBehaviour.Single} /></div>
                        <div className='absolute bottom-0 bg-white p-1 rounded-tr-xl'><DeleteIcon className='removeIcon' onClick={() => handleRemove(item)}/></div>
                    </div>

                    <div className='infoPosition'> 
                        <div className='txtCartLabel' onClick={() => navigate(getRoute(`product/${item.id}`))}>{item.labelName}</div>
                        <div className='txtCartDesc'>Колір: {item.color}</div>
                        {item.category !== 'earrings' ? <div className='txtCartDesc'>Розмір: {item.size}</div> : <></>}
                        <Price product={item} font='lg' currTypeFor='UAH'/>
                    </div>
                    
                    <div className='cart-parent-component-end'>
                        <div className='flex flex-col items-center'>
                            <button className='btnCart border'onClick={() => handleChange(item, 1)}>+</button>
                            <div className='txtCartCount'>{item.count}</div>
                            <button className='btnCart border'onClick={() => handleChange(item, -1)}>-</button>
                        </div>
                    </div>
                    
                </div>
            </div>
        )
    })

    const isEmpty = cart.data.length == 0;

    return (
        <div className='grid md:grid-cols-2 grid-cols-1 mt-[50px]'>
            <div className=''>
                <Helmet>
                    <title>Корзина: оформляй замовлення швидко, зручно та безпечно | TVOI</title>
                </Helmet>
                

                <div className='flex items-center pl-3 py-1 bg-white'>
                    <span className='w-[25px] cursor-pointer' onClick={() => navigate(-1)}><BackIcon /></span>
                    <span><CartIcon/></span>
                    <span><h5 className='font-bold text-main'>Обрані товари: &#40;{count}&#41;</h5></span>
                </div>

                <div className="cart-list">
                    {isEmpty ? <Empty/> : viewProducts}
                </div>

                <div className="fixed bottom-0 flex flex-col md:w-1/2 w-full p-3 bg-white">

                    <div className='columnStyle p-2 w-100'>

                        <div className='rowStyle'>
                            <div className='component-fill w-100'><h6 className='text-main'>Сума:</h6></div>
                            <div><h6 className='text-main'>{totalProductPrice} грн</h6></div>
                        </div>

                        <div className='rowStyle '>
                            <div className='component-fill w-100 text-secondary'><small >Знижка:</small></div>
                            <small className='text-secondary'>-{endedSale} грн</small>
                        </div>

                    </div>

                    <div className="columnStyle w-100 p-2">
                        <div className="rowStyle ">
                            <div className='component-fill w-100'><h5 className='font-bold text-main'>До сплати:</h5></div>
                            <div><h5 className='font-bold text-main'>{endedPrice} грн </h5></div>
                        </div>

                    </div>

                    <div>
                        <button 
                        className="btnConfirmCartStyle"
                        onClick={onConfirm}>
                            Оформити замовлення
                        </button>
                    </div>

                </div>
            </div>   
            {!isMobile ? <div className='ad-pos'>
                <Ad title='Ти майже у мети... Натискай на кнопку "Оформити замовлення" та мерщій далі. Ми - коти, тобі допоможемо!' image='animals/cat_2.png'/>
            </div> : <></>}
        </div>    
    );
}