import { useNavigate } from 'react-router';
import { ReactComponent as EmptyCartIcon } from '../../images/emptyCart.svg';
import { getRoute } from '../../services/routes.service';

export const Empty = () => {
    const navigate = useNavigate();
    return(
        <div className="empty-cart-container">
            <div><EmptyCartIcon/></div>
            <div className="empty-cart-text">Схоже, що корзина пуста</div>
            <button className="btn-add-cart" onClick={() => navigate(getRoute(`sales`))}>Переглянути товари</button>
        </div>
    )
}